import { tns } from "/node_modules/tiny-slider/src/tiny-slider";

const sliders = () => {
    try {
        var promoSlider = tns({
            container: '.promo_slider',
            items: 1,
            slideBy: 'page',
            autoplay: true,
            speed: 1000,
            controls: false,
            nav: false,
            autoplayButtonOutput: false,
            autoplayHoverPause: true
          });
        
          document.querySelector('.promo_left_arrow').addEventListener('click', () => {
            promoSlider.goTo('prev');
          });
        
          document.querySelector('.promo_right_arrow').addEventListener('click', () => {
            promoSlider.goTo('next');
          });
    } catch(e) {}

    try {
    var reviewsSlider = tns({
        container: '.reviews_slider',
        items: 1,
        slideBy: 'page',
        autoplay: true,
        speed: 1000,
        controls: false,
        nav: false,
        autoplayButtonOutput: false,
        autoplayHoverPause: true
        });
    
        document.querySelector('.review_btn_left').addEventListener('click', () => {
        reviewsSlider.goTo('prev');
        });
    
        document.querySelector('.review_btn_right').addEventListener('click', () => {
        reviewsSlider.goTo('next');
        });
    } catch(e) {}

    try {
        var clientsSlider = tns({
            container: '.clients_slider',
            items: 5,
            slideBy: 'page',
            autoplay: true,
            speed: 1000,
            controls: false,
            nav: false,
            autoplayButtonOutput: false,
            responsive: {
                320: {
                items: 1
                },
                577: {
                items: 2
                },
                769: {
                items: 3
                },
                993: {
                items: 4
                },
                1201: {
                items: 5
                }
            }
            });
        
            document.querySelector('.clients_btn_left').addEventListener('click', () => {
            clientsSlider.goTo('prev');
            });
        
            document.querySelector('.clients_btn_right').addEventListener('click', () => {
            clientsSlider.goTo('next');
            });  
        } catch(e) {}

};

export default sliders;