const modal = () => {
    const subscribeBtn = document.querySelector('.btn_about.subscribe'),
    subscribeOverlay = document.querySelector('#subscribe'),
    subscribeClose = document.querySelector('.subscribe_close img');

    subscribeBtn.addEventListener('click', () => {
      subscribeOverlay.style.display = 'block';
      document.body.style.cssText = "height: 100%; overflow: hidden;";
    });

    document.addEventListener('click', (e) => {
      if (e.target === subscribeOverlay || e.target === subscribeClose) {
        subscribeOverlay.style.display = 'none';
        document.body.style.cssText = "overflow: visible;";
      }
    });

    document.addEventListener('keydown', (e) => {
      if(e.key == 'Escape' || e.key == 'Enter') {
        subscribeOverlay.style.display = 'none';
        document.body.style.cssText = "overflow: visible;";
      }
    });
};

export default modal;