const registration = () => {
    const checkboxes = document.querySelectorAll('.packages .wpcf7-list-item input');
    const labelsPackages = document.querySelectorAll('.packages .wpcf7-list-item .wpcf7-list-item-label');

    checkboxes.forEach(checkbox => {
      checkbox.addEventListener('click', () => {
        labelsPackages.forEach(label => {
          label.style.backgroundColor = '#f2ead8';
        });
        const parent = checkbox.parentNode;
        const thisLabel = parent.querySelector('.wpcf7-list-item-label');
        thisLabel.style.backgroundColor = '#e2cc9d';
      });
    });
  
    const methods = document.querySelectorAll('.payment .wpcf7-list-item input');
    const labelsPayment = document.querySelectorAll('.payment .wpcf7-list-item .wpcf7-list-item-label');
  
    methods.forEach(method => {
      method.addEventListener('click', () => {
        labelsPayment.forEach(label => {
            label.style.backgroundColor = '#f2ead8';
        });
        const parent = method.parentNode;
        const thisLabel = parent.querySelector('.wpcf7-list-item-label');
        thisLabel.style.backgroundColor = '#e2cc9d';
      });
    });
  
    document.querySelector('.registration_form form').addEventListener('submit', () => {
        labelsPackages.forEach(label => {
            label.style.backgroundColor = '#f2ead8';
      });
        labelsPayment.forEach(label => {
            label.style.backgroundColor = '#f2ead8';
      });
    });
};

export default registration;